import React from 'react';
import Link from 'next/link';
import logo_main from '../img/logo5.png';
import logo_side from '../img/logo6.png';
import eng from '../img/english.png';
import thai from '../img/thailand.jpg';

if (typeof window !== 'undefined') {
  window.$ = $;
  window.jQuery = $;
  require('materialize-css');
}

class Index extends React.Component {
  componentDidMount() {
    $('.button-collapse').sideNav();
  }
  render() {
    return (
      <nav class="transparent">
        <div class="container">
          <div class="nav-wrapper">
            <img src={logo_main} className="brand-logo logo-img" />
            <a href="#" data-activates="side-nav" class="button-collapse">
              <i class="material-icons">menu</i>
            </a>
            <ul class="right hide-on-med-and-down">
              <li>
                <a class="active-link">HOME</a>
              </li>
              <li>
                <a>PORTFOLIO</a>
              </li>
              <li>
                <a>ABOUT</a>
              </li>
              <li>
                <a>RESUME</a>
              </li>
              <li>
                <a href="#contact-modal" class="modal-trigger">
                  CONTACT
                </a>
              </li>
              <li>
                <a class="flag-language" href="#">
                  <img src={eng} class="circle" height="25px" width="25px" />
                </a>
              </li>
              <li>
                <a class="flag-language" href="#">
                  <img src={thai} class="circle " height="25px" width="25px" />
                </a>
              </li>
            </ul>
            <ul id="side-nav" class="side-nav">
              <h4 class="center">
                <img src={logo_side} class="logo-img" />
              </h4>
              <li>
                <div class="divider" />
              </li>
              <li>
                <a>
                  <i class="material-icons grey-text text-darken-4">home</i>
                  HOME
                </a>
              </li>
              <li>
                <a>
                  <i class="fa fa-cog grey-text text-darken-4" /> PORTFOLIO
                </a>
              </li>
              <li>
                <a>
                  <i class="fa fa-users grey-text text-darken-4" /> ABOUT
                </a>
              </li>
              <li>
                <a>
                  <i class="fa fa-users grey-text text-darken-4" /> RESUME
                </a>
              </li>
              <li>
                <div class="divider" />
              </li>
              <li>
                <a href="#contact-modal" class="btn green modal-trigger">
                  CONTACT
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}

export default Index;
